<template>
  <div>
    <div v-if="existsEois && !emptyEois" class="chart">
      <Bar id="eoi_chart" :options="eoiChartOptions" :data="eoiChartData" />
    </div>
    <div v-if="existsTraps && !emptyTraps" class="chart">
      <Bar id="trap_chart" :options="trapChartOptions" :data="trapChartData" />
    </div>
    <div v-if="existsLab && !emptyLab" class="chart">
      <Bar id="lab_chart" :options="labChartOptions" :data="labChartData" />
    </div>
  </div>
</template>

<script>
import { Bar } from "vue-chartjs";
import Chart from "chart.js/auto";
import "chartjs-adapter-date-fns";
import zoomPlugin from "chartjs-plugin-zoom";

Chart.register(zoomPlugin);

export default {
  name: "VisitObservationsCharts",
  components: {
    Bar,
  },
  props: {
    visit_json: Object,
    lab: Object,
  },
  data() {
    return {};
  },
  computed: {
    existsEois() {
      return this.visit_json?.eois != undefined;
    },
    emptyEois() {
      return this.eoiChartData.datasets[0].data.every(
        (element) => element == 0
      );
    },
    existsTraps() {
      return this.visit_json?.traps != undefined;
    },
    emptyTraps() {
      return this.trapChartData.datasets[0].data.every(
        (element) => element == 0
      );
    },
    existsLab() {
      return this.lab?.eois != undefined;
    },
    emptyLab() {
      return this.labChartData.datasets[0].data.every(
        (element) => element == 0
      );
    },
    eoiChartData() {
      if (!this.existsEois) return;
      let labels = [];
      let datasets = [];
      //populate labels
      for (let [key1, value1] of Object.entries(this.visit_json.eois[0])) {
        for (let [key2, value2] of Object.entries(value1)) {
          let label = `${key1} / ${key2}`;
          labels.push(label);
        }
      }

      //create dataset
      datasets.push({
        label: "EOIs",
        data: Array(labels.length).fill(0),
        backgroundColor: "rgba(255, 99, 132, 0.2)",
        borderColor: "rgba(255, 99, 132, 1)",
        borderWidth: 1,
      });
      //populate dataset
      for (let [key1, value1] of Object.entries(this.visit_json.eois)) {
        for (let [key2, value2] of Object.entries(value1)) {
          for (let [key3, value3] of Object.entries(value2)) {
            let label = `${key2} / ${key3}`;
            let i = labels.findIndex((element) => element == label);
            let value;
            if (isNaN(value3[0].value)) {
              value3[0].value == "true" ? (value = 1) : (value = 0);
            } else {
              value = parseInt(value3[0].value);
            }
            datasets[0].data[i] += value;
          }
        }
      }
      return {
        labels: labels,
        datasets: datasets,
      };
    },
    eoiChartOptions() {
      return {
        responsive: true,
        indexAxis: "y",
        scales: {
          x: {
            beginAtZero: true,
            ticks: {
              stepSize: 1,
            },
          },
        },
      };
    },
    trapChartData() {
      if (!this.existsTraps) return;
      let labels = [];
      let datasets = [];
      //populate labels
      labels = Object.entries(this.visit_json.traps).map(([key, value]) => key);
      //create dataset
      datasets.push({
        label: "Capturas",
        data: Array(labels.length).fill(0),
        backgroundColor: "rgba(99, 132, 255, 0.2)",
        borderColor: "rgba(99, 132, 255, 1)",
        borderWidth: 1,
      });
      //populate dataset
      for (let [key1, value1] of Object.entries(this.visit_json.traps)) {
        let i = labels.findIndex((element) => element == key1);
        let value = parseInt(value1.value);
        datasets[0].data[i] = value;
      }
      return {
        labels: labels,
        datasets: datasets,
      };
    },
    trapChartOptions() {
      return {
        responsive: true,
        indexAxis: "y",
        scales: {
          x: {
            beginAtZero: true,
            ticks: {
              stepSize: 1,
            },
          },
        },
      };
    },
    labChartData() {
      if (!this.existsLab) return;
      let labels = [];
      let datasets = [];

      //populate labels
      for (let [key1, value1] of Object.entries(this.lab.eois[0])) {
        for (let [key2, value2] of Object.entries(value1)) {
          for (let obj of value2) {
            let label = `${key1} / ${key2} / ${obj.description}`;
            labels.push(label);
          }
        }
      }
      //create dataset
      datasets.push({
        label: "Filoxera",
        data: Array(labels.length).fill(0),
        backgroundColor: "rgba(99, 255, 132, 0.2)",
        borderColor: "rgba(99, 255, 132, 1)",
        borderWidth: 1,
      });
      //populate dataset
      for (let [key1, value1] of Object.entries(this.lab.eois)) {
        for (let [key2, value2] of Object.entries(value1)) {
          for (let [key3, value3] of Object.entries(value2)) {
            for (let obj of value3) {
              let label = `${key2} / ${key3} / ${obj.description}`;
              console.log(label);
              let i = labels.findIndex((element) => element == label);
              let value;
              if (isNaN(obj.value)) {
                obj.value == "true" ? (value = 1) : (value = 0);
              } else {
                value = parseInt(obj.value);
              }
              datasets[0].data[i] += value;
            }
          }
        }
      }
      return {
        labels: labels,
        datasets: datasets,
      };
    },
    labChartOptions() {
      return {
        responsive: true,
        indexAxis: "y",
        scales: {
          x: {
            beginAtZero: true,
            ticks: {
              stepSize: 1,
            },
          },
        },
      };
    },
  },
};
</script>

<style scoped>
.chart {
  width: 700px;
}
</style>
