<template>
  <v-dialog v-model="localDialog" @afterLeave="reset()" width="auto">
    <v-progress-circular
      v-if="isLoading"
      color="secondary"
      size="65"
      indeterminate
    ></v-progress-circular>
    <v-card v-else class="mx-auto" max-height="900" min-width="800">
      <v-card-title
        class="bg-info d-flex justify-space-between align-center"
      >
        <span
          >{{ `${visit.plotName} (${visit.plot}) - ` }}
          <span class="text-decoration-underline"
            >{{ `${visit.dateDay} ${visit.dateMonth}` }}
            <v-tooltip activator="parent" location="bottom">{{
              `${new Date(visit.tBegin).toLocaleTimeString(
                "pt"
              )} - ${new Date(visit.tEnd).toLocaleTimeString("pt")} (${
                visit.duration
              })`
            }}</v-tooltip></span
          ></span
        >
        <v-spacer></v-spacer>
        <div class="d-flex align-center">
          <v-avatar v-if="visit.ef" color="surface" class="mr-2">
            <strong class="text-medium-emphasis">{{ visit.ef }}</strong>
            <v-tooltip activator="parent" location="bottom"
              >Estado fenolgico</v-tooltip
            ></v-avatar
          >
          <v-sheet
            v-if="visit.nea != '--' && visit.nea"
            color="surface"
            class="py-1 px-4 mr-2"
            rounded="xl"
          >
            <strong class="text-medium-emphasis">{{ visit.nea }}</strong>
            <v-tooltip activator="parent" location="bottom"
              >Nvel Econmico de Ataque</v-tooltip
            ></v-sheet
          >
          <v-sheet
            v-if="visit.rain || visit.treatment"
            color="surface"
            class="mr-2 d-flex align-center jsutify-space-around"
            rounded="xl"
          >
            <v-avatar v-if="visit.rain">
              <v-icon color="info">mdi-weather-pouring</v-icon>
              <v-tooltip activator="parent" location="bottom"
                >Chuva</v-tooltip
              ></v-avatar
            >
            <v-avatar v-if="visit.treatment"
              ><v-icon color="success">mdi-spray</v-icon>
              <v-tooltip activator="parent" location="bottom"
                >Tratamento</v-tooltip
              ></v-avatar
            >
          </v-sheet>
          <v-avatar v-if="visit.technicians" color="secondary-lighten-1">
            <strong class="text-medium-emphasis">{{
              visit.techsShort
            }}</strong>
            <v-tooltip activator="parent" location="bottom">{{
              visit.technicians
            }}</v-tooltip>
          </v-avatar>
        </div>
      </v-card-title>
      <v-tabs color="secondary" v-model="tab" grow>
        <v-tab value="rawdata">Dados</v-tab>
        <v-tab v-if="hasPhotos" value="media">Fotos</v-tab>
        <v-tab v-if="hasGeojson" value="map">Mapa</v-tab>
      </v-tabs>
      <v-card-text>
        <v-tabs-window v-model="tab">
          <v-tabs-window-item value="rawdata">
            <VisitObservations
              :folderId="visit.fileId"
              :region_code="visit.plot"
              :visit_date="visit.date"
              :visit_json="visit_json"
              :lab="lab"
              @refresh="refreshVisitDetail"
            ></VisitObservations>
          </v-tabs-window-item>
          <v-tabs-window-item value="media">
            <VisitMedia :photos="photos"></VisitMedia>
          </v-tabs-window-item>
          <v-tabs-window-item value="map">
            <VisitMap
              :geojson="geojson"
              :photos="photos"
              :region_polygon="region_polygon"
            ></VisitMap>
          </v-tabs-window-item>
        </v-tabs-window>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import VisitObservations from "./VisitObservations.vue";
import VisitMedia from "./VisitMedia.vue";
import VisitMap from "./VisitMap.vue";

import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";
export default {
  name: "VisitDetail",
  components: {
    VisitObservations,
    VisitMedia,
    VisitMap,
  },
  props: {
    visit: {
      type: Object,
      // required: true,
    },
    dialog: {
      type: Boolean,
      required: true,
    },
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();

    return { generalStore, accountStore };
  },
  data() {
    return {
      isLoading: false,
      visit_json: null,
      lab: [],
      photos: [],
      geojson: null,
      region_polygon: null,

      tab: null,
    };
  },
  watch: {
    dialog(value) {
      if (value) {
        this.onOpenDialog();
      }
    },
  },
  computed: {
    localDialog: {
      get() {
        return this.dialog;
      },
      set(value) {
        this.$emit("update:dialog", value);
      },
    },
    hasPhotos() {
      return this.photos.length > 0;
    },
    hasGeojson() {
      return this.geojson?.features.length > 0;
    },
  },
  methods: {
    async onOpenDialog() {
      this.isLoading = true;
      await Promise.all([
        this.getVisitDetail(),
        this.getRegionGeometry(),
      ]);
      this.isLoading = false;
    },
    reset() {
      this.visit_json = null;
      this.lab = [];
      this.photos = [];
      this.isLoading = false;
      this.geojson = null;
      this.region_polygon = null;
      this.tab = null;
    },
    async refreshVisitDetail() {
      this.isLoading = true;
      await this.getVisitDetail();
      this.isLoading = false;
    },
    convertSecToMin(value) {
      let time = new Date(value * 1000).toISOString().substr(11, 8);
      const hours = time.split(":")[0];
      const minutes = time.split(":")[1];
      const seconds = time.split(":")[2];
      if (hours != "00") {
        return hours + "h " + minutes + "m " + seconds + "s"; // hh:mm:ss
      } else if (minutes != "00") {
        return minutes + "m " + seconds + "s"; // mm:ss
      } else return seconds + "s"; // ss
    },

    async getVisitDetail() {
      await axios
        .request({
          url: "api/observation_detail",
          method: "GET",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
          params: {
            fileId: this.visit.fileId,
          },
        })
        .then((response) => {
          this.visit_json = response.data.visit;
          this.lab = response.data.lab;
          this.photos = response.data.photos;
          this.geojson = response.data.geojson;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar(
            "Erro ao carregar detalhes da observao."
          );
          // this.localDialog = false;
        });
    },
    async getRegionGeometry() {
      await axios
        .request({
          url: `api/regions/geometry/${this.visit.plot}`,
          method: "GET",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
        })
        .then((response) => {
          if (!response.data.geometry_shape) {
            this.generalStore.errorSnackbar("Regio sem geometria definida.");
            return;
          }
          this.region_polygon = response.data.geometry_shape;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar(
            "Erro ao carregar geometria da regio."
          );
          // this.localDialog = false;
        });
    },
  },
};
</script>

<style></style>
