<template>
  <v-hover v-slot="{ isHovering, props }">
    <v-card
      v-bind="props"
      class="d-flex cursor-pointer"
      :elevation="isHovering ? 16 : 4"
      @click="$emit('detail', vis)"
    >
      <v-card-title
        :class="vis.visited ? 'bg-primary pa-2' : 'bg-surface-variant pa-2'"
      >
        <div class="d-flex flex-column align-center">
          <span>{{ vis.dateDay }}</span>
          <span>
            {{ vis.dateMonth }}
          </span>
          <v-divider class="align-self-stretch my-3"></v-divider>
          <span>{{ vis.plot }}</span>
        </div>
        <div
          class="mt-3 d-flex justify-space-around text-caption text-medium-emphasis"
        >
          <span>
            <v-icon size="large">mdi-camera</v-icon>{{ vis.numPhotos }}</span
          >
          <!-- <span>
            <v-icon size="large">mdi-microphone</v-icon>
            {{ vis.numVoice }}
          </span> -->
        </div>
      </v-card-title>
      <v-card-text class="bg-background-dark pa-2">
        <v-row class="px-2">
          <v-col cols="10" class="text-subtitle-1">
            <span class="mr-2"><strong>EF:</strong> {{ vis.ef }}</span>
            <span v-if="visit.nea != 'null'"
              ><strong>NEA:</strong> {{ vis.nea }}</span
            ></v-col
          >
          <v-col cols="2" class="d-flex justify-center align-center">
            <v-avatar size="x-small" v-if="vis.rain">
              <v-icon color="info">mdi-weather-pouring</v-icon>
              <v-tooltip activator="parent" location="bottom">Chuva</v-tooltip>
            </v-avatar>
            <v-avatar size="x-small" v-if="vis.treatment">
              <v-icon color="success">mdi-spray</v-icon>
              <v-tooltip activator="parent" location="bottom"
                >Tratamento</v-tooltip
              >
            </v-avatar>
          </v-col>
        </v-row>
        <v-divider class="border-opacity-25"></v-divider>
        <div :style="{ height: '60%' }" class="d-flex align-center">
          <v-row v-if="vis.visited && (vis.eois || vis.captures)">
            <v-col cols="12" v-if="vis.eois"
              ><span class="font-weight-bold text-secondary">EOIs: </span
              >{{ vis.eois }}</v-col
            >
            <v-col cols="12" v-if="vis.captures"
              ><span class="font-weight-bold text-secondary">Capturas: </span
              >{{ vis.captures }}</v-col
            >
          </v-row>
          <v-row v-else-if="vis.visited && !vis.eois && !vis.captures">
            <v-col cols="12" class="text-center">
              <span>Sem Observaes</span>
            </v-col>
          </v-row>
          <v-row v-if="!vis.visited">
            <v-col cols="12" class="text-center">
              <span>Visita no efetuada</span>
            </v-col>
          </v-row>
          <div>
            <v-avatar size="x-small" v-if="!vis.gps">
              <v-icon color="error">mdi-map-marker-off</v-icon>
              <v-tooltip activator="parent" location="bottom"
                >Sem trajtoria GPS</v-tooltip
              ></v-avatar
            >
          </div>
        </div>
        <div clas="pa-1 ">
          <v-row>
            <v-col cols="10" class="d-flex align-center">
              <span
                v-if="vis.notes"
                class="bg-surface pa-1 d-inline-block text-truncate rounded"
                style="width: 210px"
                >{{ vis.notes }}
                <v-tooltip activator="parent" location="bottom">{{
                  vis.notes
                }}</v-tooltip>
              </span>
            </v-col>
            <v-col cols="2" class="d-flex justify-center align-center">
              <v-avatar
                v-if="vis.technicians"
                class="mr-5"
                color="secondary-lighten-1"
                size="small"
                ><strong class="text-medium-emphasis">{{
                  vis.techsShort
                }}</strong>
                <v-tooltip activator="parent" location="bottom">{{
                  vis.technicians
                }}</v-tooltip>
              </v-avatar></v-col
            >
          </v-row>
        </div>
      </v-card-text>
    </v-card>
  </v-hover>
</template>

<script>
export default {
  name: "VisitCard",
  props: {
    visit: Object,
  },
  emits: ["detail"],
  data() {
    return {};
  },
  computed: {
    vis() {
      const hasTechs = this.visit.technicians != null;
      const isCollab = this.visit.technicians?.includes(";");
      const overallArr =
        this.visit.overall != "null" && this.visit.overall
          ? this.visit.overall?.split("//")
          : [];
      return {
        fileId: this.visit.fileId,
        visited: this.visit.visited,
        rain: this.visit.rain,
        treatment: this.visit.treatment,
        gps: this.visit.gps,
        date: this.visit.date,
        dateDay: new Date(this.visit.date).toLocaleDateString("pt-PT", {
          day: "2-digit",
        }),
        dateMonth:
          this.getMonthShort(new Date(this.visit.date).getMonth()) +
          " " +
          new Date(this.visit.date).getFullYear(),
        plot: this.visit.plot,
        plotName: this.visit.plotName,
        numPhotos: this.visit.numPhotos,
        numVoice: this.visit.numVoice,
        tBegin: this.visit.tBegin,
        tEnd: this.visit.tEnd,
        duration: this.visit.duration,
        // durationEffective: this.visit.durationEffective,
        ef: this.visit.ef != null ? this.visit.ef : "--",
        nea: this.visit.nea != null ? this.visit.nea + "%" : "--",
        eois: overallArr.at(0)?.trim() != "" ? overallArr.at(0) : null,
        captures: overallArr.at(1)?.trim() != "" ? overallArr.at(1) : null,
        notes: this.visit.notes != "null" ? this.visit.notes : null,
        techsShort: hasTechs
          ? isCollab
            ? "&"
            : Array.from(this.visit.technicians).at(0)
          : null,
        technicians:
          this.visit.technicians != "null" ? this.visit.technicians : null,
      };
    },
  },
  methods: {
    getMonthShort(numeric) {
      switch (numeric) {
        case 0:
          return "Jan";
        case 1:
          return "Fev";
        case 2:
          return "Mar";
        case 3:
          return "Abr";
        case 4:
          return "Mai";
        case 5:
          return "Jun";
        case 6:
          return "Jul";
        case 7:
          return "Ago";
        case 8:
          return "Set";
        case 9:
          return "Out";
        case 10:
          return "Nov";
        case 11:
          return "Dez";
        default:
          return "Ms";
      }
    },
  },
};
</script>

<style scoped>
.notes {
  font-size: 0.8rem;
  width: "200px" !important;
}
</style>
