<template>
  <v-sheet height="500px" width="1000">
    <MapboxMap
      ref="mapboxMap"
      :accessToken="accessToken"
      mapStyle="mapbox://styles/mapbox/satellite-streets-v12"
      @mb-created="(mapInstance) => (map = mapInstance)"
      :center="mapCenter"
      :zoom="16"
      :style="{ height: '100%' }"
    ></MapboxMap>
  </v-sheet>
</template>

<script>
import { MapboxMap } from "@studiometa/vue-mapbox-gl";
import mapboxgl from "mapbox-gl";

export default {
  name: "VisitMap",
  components: { MapboxMap },
  props: {
    geojson: Object,
    photos: Array,
    region_polygon: Object,
  },
  setup() {
    const popup = new mapboxgl.Popup({
      closeButton: true,
      closeOnClick: true,
    });
    const accessToken = process.env.VUE_APP_MAPBOX_TOKEN;
    return { popup, accessToken };
  },
  data() {
    return {
      map: null,
    };
  },
  computed: {
    mapCenter() {
      if (!this.region_polygon) return [-9, -39];
      const coords = this.region_polygon.coordinates[0];
      const x = coords.map((coord) => coord[0]);
      const y = coords.map((coord) => coord[1]);
      const x_min = Math.min(...x);
      const x_max = Math.max(...x);
      const y_min = Math.min(...y);
      const y_max = Math.max(...y);
      return [(x_min + x_max) / 2, (y_min + y_max) / 2];
    },
    regionGeojson() {
      return {
        type: "Feature",
        properties: {},
        geometry: this.region_polygon,
      };
    },
    path() {
      if (!this.geojson) return;
      let json = {
        type: "FeatureCollection",
        features: [],
      };
      const multistring = this.geojson.features.filter(
        (feature) => feature.geometry.type === "MultiLineString"
      );
      if (multistring.length > 0) {
        multistring.forEach((feature) => {
          let lines = feature.geometry.coordinates.map((line) => {
            return {
              type: "Feature",
              geometry: {
                type: "LineString",
                coordinates: line,
              },
            };
          });
          json.features.push(...lines);
        });
      } else {
        json = {
          type: "FeatureCollection",
          features: this.geojson.features.filter(
            (feature) => feature.geometry.type === "LineString"
          ),
        };
      }
      return json;
    },
    points() {
      if (!this.geojson) return;
      return {
        type: "FeatureCollection",
        features: this.geojson.features.filter(
          (feature) =>
            feature.geometry.type === "Point" &&
            feature.properties.name.endsWith(".jpg")
        ),
      };
    },
  },
  methods: {
    getPhoto(name) {
      return this.photos.find((photo) => photo.name == name);
    },
  },
  mounted() {
    console.log(this.geojson);
    console.log(this.path);
    this.map.on("load", () => {
      this.map.addSource("region", {
        type: "geojson",
        data: this.regionGeojson,
      });
      this.map.addLayer({
        id: "region",
        type: "fill",
        source: "region",
        layout: {},
        paint: {
          "fill-color": "#088",
          "fill-opacity": 0.3,
        },
      });
      this.map.addSource("path", {
        type: "geojson",
        data: this.path,
      });
      this.map.addLayer({
        id: "path",
        type: "line",
        source: "path",
        layout: {},
        paint: {
          "line-color": "#FB8C00",
          "line-width": 2,
        },
      });
      this.map.addSource("points", {
        type: "geojson",
        data: this.points,
      });
      this.map.addLayer({
        id: "points",
        type: "circle",
        source: "points",
        paint: {
          "circle-radius": 6,
          "circle-color": "#2196F3",
        },
      });
      this.map.on("mouseenter", "points", (e) => {
        this.map.getCanvas().style.cursor = "pointer";
        let photo_time = new Date(
          e.features[0].properties.time
        ).toLocaleTimeString("pt-PT");
        const html = `
          <div>
            <h4>Click para ver a foto</h4>
            ${photo_time}
          </div>
        `;
        this.popup.setLngLat(e.lngLat).setHTML(html).addTo(this.map);
      });
      this.map.on("click", "points", (e) => {
        console.log(e.features[0].properties);
        let photo_name = e.features[0].properties.name;
        let photo = this.getPhoto(photo_name);
        if (photo) {
          let view_url = photo.view;
          var win = window.open(view_url, "_blank");
          win.focus();
        }
      });
      this.map.on("mouseleave", "points", () => {
        this.map.getCanvas().style.cursor = "";
        this.popup.remove();
      });
    });
  },
};
</script>

<style></style>
