<template>
  <v-sheet>
    <span class="text-h6">Notas</span>
    <table class="example-table my-2" id="note_table"></table>
    <a id="no_notes" style="display: none">Sem notas</a>
    <v-tabs color="info" v-model="tab" align-tabs="center">
      <v-tab value="tables">Tabelas</v-tab>
      <v-tab value="charts">Grficos</v-tab>
    </v-tabs>
    <v-tabs-window v-model="tab" class="scrollable">
      <v-tabs-window-item value="tables">
        <!-- <div>
          <span class="text-h6">EOIs</span>
          <table class="example-table my-2" id="eoi_table"></table>
          <a id="no_data_eoi" style="display: none">Sem dados</a>
          <span class="text-h6">Capturas</span>
          <table class="example-table my-2" id="trap_table"></table>
          <a id="no_data_trap" style="display: none">Sem dados</a>
          <div id="lab_div" style="display: none">
            <span class="text-h6" id="lab_header">Filoxera</span>

            <table class="example-table mt-2" id="lab_table">
         
            </table>
            <a id="no_data_lab" style="display: none">Sem registos</a>
          </div>
        </div> -->
        <VisitObservationsTables
          :folderId="folderId"
          :region_code="region_code"
          :visit_date="visit_date"
          :visit_json="visit_json"
          :lab="lab"
          @refresh="$emit('refresh')"
        />
      </v-tabs-window-item>
      <v-tabs-window-item value="charts">
        <VisitObservationsCharts
          :visit_json="visit_json"
          :lab="lab"
        ></VisitObservationsCharts>
      </v-tabs-window-item>
    </v-tabs-window>
  </v-sheet>
</template>

<script>
import VisitObservationsCharts from "./VisitObservationsCharts.vue";
import VisitObservationsTables from "./VisitObservationsTables.vue";
export default {
  name: "VisitObservations",
  components: {
    VisitObservationsCharts,
    VisitObservationsTables,
  },
  props: {
    folderId: String,
    region_code: String,
    visit_date: String,
    visit_json: Object,
    lab: Object,
  },
  data() {
    return {
      tab: "tables",
    };
  },
  watch: {},
  computed: {},
  methods: {
    getObservationText(observation) {
      if (observation != null) {
        let processedObservation = null;
        try {
          let infoJSON = JSON.parse(observation);
          if (infoJSON["eoi"] != undefined) {
            let message =
              "rvore " +
              (parseInt(infoJSON["eoi"]) + 1) +
              " " +
              infoJSON["protocol_name"] +
              ": " +
              infoJSON["observation_name"];
            if (observation.includes("title"))
              message += " - " + infoJSON["title"];
            processedObservation = message;
          } else if (infoJSON["title"] != undefined) {
            processedObservation = "Armadilha: " + infoJSON["title"];
          } else if (infoJSON["occurrence_type"] != undefined) {
            let occ_type =
              parseInt(infoJSON["occurrence_type"]) === 0
                ? "Ao: "
                : "Evento: ";
            processedObservation = occ_type + infoJSON["occurrence_name"];
          }
          return processedObservation;
        } catch (e) {
          console.log(e);

          return undefined;
        }
      }
      return undefined;
    },
    fillNotes(genericNote, specificNotes) {
      var note_table = document.getElementById("note_table");

      if (genericNote == undefined && specificNotes == undefined) {
        note_table.style.display = "none";
        document.getElementById("no_notes").style.display = "block";
        return;
      }

      let row = note_table.insertRow();
      if (genericNote != undefined) {
        let type = document.createTextNode("Genrica");
        let timestamp = document.createTextNode("- - -");
        let valueText = document.createTextNode(genericNote.value);

        let cell = row.insertCell();
        cell.style.textAlign = "center";
        cell.appendChild(type);
        cell = row.insertCell();
        cell.style.textAlign = "center";
        cell.appendChild(timestamp);
        cell = row.insertCell();
        cell.style.textAlign = "center";
        cell.appendChild(valueText);
      }

      if (specificNotes != undefined) {
        Object.keys(specificNotes)
          .sort()
          .forEach(function (noteIndex) {
            if (specificNotes[noteIndex].id.includes("_note_")) {
              let row = note_table.insertRow();
              let cell;
              let observation = specificNotes[noteIndex]["observation"];
              observation = getObservationText(observation);
              if (observation != undefined) {
                let observationText = document.createTextNode(observation);
                cell = row.insertCell();
                cell.style.textAlign = "center";
                cell.appendChild(observationText);
              } else {
                let empty = document.createTextNode("- - -");
                cell = row.insertCell();
                cell.style.textAlign = "center";
                cell.appendChild(empty);
              }

              let timestamp = specificNotes[noteIndex]["creation_timestamp"]; //20210426_092643
              let timestampText = document.createTextNode(
                timestamp.substr(9, 2) + ":" + timestamp.substr(11, 2)
              );

              cell = row.insertCell();
              cell.style.textAlign = "center";
              cell.appendChild(timestampText);

              let value = specificNotes[noteIndex]["content"];
              let valueText = document.createTextNode(value);

              cell = row.insertCell();
              cell.appendChild(valueText);
            }
          });
      }

      this.generateTableHead(note_table, ["Observao", "Hora", "Nota"]);
    },
    getProcessedEois(rawJson) {
      var maxEoiCount = 0;
      var map = {};
      var flagPerProtocol = {};
      console.log(rawJson);

      var rawDate = rawJson["time_begin"].split(".")[0];
      var defaultObservations = rawJson["info"]["Default_observations"];
      var caliber = defaultObservations["Calibre"];
      var genericNote = defaultObservations["Notas"];

      // document.getElementById("date").innerText = rawDate.substring(0,4) + "/" + rawDate.substring(4,6) + "/" + rawDate.substring(6,8);
      // document.getElementById("pob").innerText = rawJson['plot_name'];
      // document.getElementById("crop").innerText = rawJson['crops'];
      // document.getElementById("ef").innerText = defaultObservations['EF'].value;
      // document.getElementById("rain").innerText = defaultObservations['Chuva'].value  === "true" ? "Sim" : "No";
      // document.getElementById("treatment").innerText = defaultObservations['Tratamento'].value  === "true" ? "Sim" : "No";
      // document.getElementById("caliber").innerText = typeof  caliber === 'undefined' ? 'No registado' : caliber.value;
      // //document.getElementById("notes").innerText = typeof  notes === 'undefined' ? 'No registado' : notes.value;

      this.fillNotes(genericNote, rawJson["notes"]);

      var eoi_table = document.getElementById("eoi_table");

      var eoiJson = rawJson["eois"];
      if (eoiJson != undefined) {
        Object.keys(eoiJson).forEach(function (eoi) {
          var eoiInt = parseInt(eoi);

          var protocolsByEoi = eoiJson[eoiInt];
          if (maxEoiCount < eoiInt) maxEoiCount = eoiInt;
          Object.keys(protocolsByEoi).forEach(function (protocol) {
            var obsPerProtocol = protocolsByEoi[protocol];

            if (typeof flagPerProtocol[protocol] === "undefined") {
              flagPerProtocol[protocol] = false;
            }

            Object.keys(obsPerProtocol).forEach(function (obs) {
              var componentPerObservation = obsPerProtocol[obs];
              Object.keys(componentPerObservation).forEach(function (
                component
              ) {
                var componentData = componentPerObservation[component];

                var description = componentData["description"];
                var rawValue = componentData["value"];

                if (rawValue === "true") rawValue = "1";
                else if (rawValue === "false") rawValue = "0";

                if (rawValue !== "0" && flagPerProtocol[protocol] === false) {
                  flagPerProtocol[protocol] = true;
                }

                if (map[protocol] == undefined) map[protocol] = {};
                if (map[protocol][obs] == undefined) map[protocol][obs] = {};
                if (map[protocol][obs][description] == undefined)
                  map[protocol][obs][description] = [];

                map[protocol][obs][description][eoiInt] = rawValue;
              });
            });
          });
        });
        Object.keys(flagPerProtocol).forEach(function (protocol) {
          if (flagPerProtocol[protocol] === false) delete map[protocol];
        });

        if (Object.keys(map).length !== 0) {
          const arr = Array.from(
            { length: maxEoiCount + 1 },
            (_, index) => index + 1
          );
          let data = ["Protocolo", "Observao", "rvore n."];
          for (var i = 0; i < arr.length; i++) {
            data.push(arr[i].toString());
          }
          this.generateEOITable(eoi_table, map);
          this.generateTableHead(eoi_table, data);
        } else {
          eoi_table.style.display = "none";
          document.getElementById("no_data_eoi").style.display = "block";
        }
      } else {
        eoi_table.style.display = "none";
        document.getElementById("no_data_eoi").style.display = "block";
      }

      var trap_table = document.getElementById("trap_table");

      var trapJson = rawJson["traps"];
      if (trapJson != undefined) {
        let trapKeys = Object.keys(trapJson).sort();
        let nonZeros = this.generateTrapTable(trap_table, trapJson);
        this.generateTableHead(trap_table, trapKeys);
        if (nonZeros === 0) {
          trap_table.style.display = "none";
          document.getElementById("no_data_trap").style.display = "block";
        }
      } else {
        trap_table.style.display = "none";
        document.getElementById("no_data_trap").style.display = "block";
      }
    },
    getProcessedLab(lab) {
      document.getElementById("lab_div").style.display = "block";
      var eoiJson = lab["eois"];
      var maxEoiCount = 0;
      var map = {};

      console.log(lab["info"]);

      try {
        var nea = lab["info"]["nea"];
        if (nea != undefined) {
          document.getElementById("nea").innerText = nea + "%";
          document.getElementById("nea_div").style.display = "block";
        }
      } catch (error) {
        console.error(error);
      }

      try {
        var date_filo =
          lab["info"]["Observations_per_portocol"]["Filoxera (Fase 4)"][0]
            .value;
        if (date_filo != undefined) {
          var splitDate = date_filo.split("/");
          var processedDate =
            splitDate[2] + "/" + splitDate[1] + "/" + splitDate[0];
          document.getElementById("date_filo").innerText = processedDate;
          document.getElementById("date_filo_div").style.display = "block";
        }
      } catch (error) {
        console.error(error);
      }

      Object.keys(eoiJson).forEach(function (eoi) {
        var eoiInt = parseInt(eoi);

        var protocolsByEoi = eoiJson[eoiInt];
        if (maxEoiCount < eoiInt) maxEoiCount = eoiInt;
        Object.keys(protocolsByEoi).forEach(function (protocol) {
          var obsPerProtocol = protocolsByEoi[protocol];
          protocol = protocol.replace("Filoxera (", "");
          protocol = protocol.substring(0, protocol.length - 1);
          Object.keys(obsPerProtocol).forEach(function (obs) {
            var componentPerObservation = obsPerProtocol[obs];
            Object.keys(componentPerObservation).forEach(function (component) {
              var componentData = componentPerObservation[component];

              var description = componentData["description"];
              var rawValue = componentData["value"];

              if (rawValue === "true") rawValue = "Sim";
              else if (rawValue === "false") rawValue = "No";

              if (map[protocol] == undefined) map[protocol] = {};
              if (map[protocol][obs] == undefined) map[protocol][obs] = {};
              if (map[protocol][obs][description] == undefined)
                map[protocol][obs][description] = [];

              map[protocol][obs][description][eoiInt] = rawValue;
            });
          });
        });
      });

      var lab_table = document.getElementById("lab_table");

      const arr = Array.from(
        { length: maxEoiCount + 1 },
        (_, index) => index + 1
      );
      let data = ["Fase", "Observao", "rvore n."];
      for (var i = 0; i < arr.length; i++) {
        data.push(arr[i].toString());
      }
      this.generateEOITable(lab_table, map);
      this.generateTableHead(lab_table, data);
      console.log(data);
    },
    generateTableHead(table, data) {
      let thead = table.createTHead();
      let row = thead.insertRow();
      for (let key of data) {
        let th = document.createElement("th");
        let text = document.createTextNode(key);
        th.appendChild(text);
        row.appendChild(th);
      }
    },
    generateEOITable(table, data) {
      Object.keys(data)
        .sort()
        .forEach(function (protocol) {
          var obsPerProtocol = data[protocol];
          let row = table.insertRow();

          let protocolText = document.createTextNode(protocol);
          let protocolCell = row.insertCell();
          protocolCell.appendChild(protocolText);
          protocolCell.style.textAlign = "center";

          var obCount = 0;
          Object.keys(obsPerProtocol).forEach(function (ob) {
            var componentsPerObservation = obsPerProtocol[ob];

            if (obCount !== 0) {
              row = table.insertRow();
              row.insertCell();
            }
            let obText = document.createTextNode(ob);
            let obCell = row.insertCell();
            obCell.appendChild(obText);
            obCell.style.textAlign = "center";

            var descriptionCount = 0;

            Object.keys(componentsPerObservation).forEach(function (
              description
            ) {
              var arrayOfValues = componentsPerObservation[description];

              if (descriptionCount !== 0) {
                row = table.insertRow();
                row.insertCell();
                row.insertCell();
              }

              let descriptionText;
              if (description !== ob)
                descriptionText = document.createTextNode(description);
              else descriptionText = document.createTextNode("- - -");
              let descriptionCell = row.insertCell();
              descriptionCell.appendChild(descriptionText);
              descriptionCell.style.textAlign = "center";

              for (var i = 0; i < arrayOfValues.length; i++) {
                let valueText;

                if (arrayOfValues[i] == undefined)
                  valueText = document.createTextNode("");
                else valueText = document.createTextNode(arrayOfValues[i]);

                var cell = row.insertCell();

                cell.style.textAlign = "center";

                cell.appendChild(valueText);
                if (
                  arrayOfValues[i] !== "0" &&
                  arrayOfValues[i] !== "No" &&
                  arrayOfValues[i] != undefined
                )
                  cell.style.backgroundColor = "#99FF00";
              }

              descriptionCount++;
            });

            obCount++;
          });
        });
    },
    generateTrapTable(table, map) {
      var nonZeros = 0;
      let row = table.insertRow();

      Object.keys(map)
        .sort()
        .forEach(function (trapName) {
          var value = map[trapName]["value"];
          let valueText = document.createTextNode(value);
          let cell = row.insertCell();
          cell.style.textAlign = "center";
          cell.appendChild(valueText);
          if (value !== "0") {
            cell.style.backgroundColor = "#99FF00";
            nonZeros++;
          }
        });
      return nonZeros;
    },
  },
  mounted() {
    if (this.visit_json != undefined)
      this.fillNotes(
        this.visit_json?.info?.Default_observations?.Notas,
        this.visit_json.notes
      );
  },
  // mounted() {
  //   let exists_visit = this.visit_json != undefined;
  //   let exists_lab = this.lab?.eois != undefined;

  //   if (exists_visit) this.getProcessedEois(this.visit_json);

  //   if (exists_lab) this.getProcessedLab(this.lab);
  // },
};
</script>

<style scoped>
.example-table {
  border: 2px solid #cccccc;
  border-spacing: 30px;
  border-collapse: separate;
}
.scrollable {
  overflow-y: auto;
  max-height: 500px;
}
</style>
