<template>
  <v-container>
    <v-alert type="warning"
      >Para corrigir os valores <strong>clique</strong> na cluda da tabela a
      corrigir.</v-alert
    >
    <v-card v-if="existsEois" background-color="secondary" class="mt-4">
      <v-card-title class="d-flex justify-space-between align-center"
        ><span>EOIs</span>
        <v-btn variant="plain" color="primary" icon @click="showEOI = !showEOI">
          <v-icon>{{ showEOI ? "mdi-chevron-up" : "mdi-chevron-down" }}</v-icon>
        </v-btn>
      </v-card-title>
      <v-expand-transition>
        <div v-show="showEOI">
          <v-card-text
            ><v-data-table-virtual
              :headers="eoiTableHeaders"
              :items="eoiTableItems"
            >
              <template v-slot:item="row">
                <tr>
                  <td>
                    <strong>{{ row.item.protocol }}</strong>
                  </td>
                  <td>
                    <strong>{{ row.item.observation }}</strong>
                  </td>
                  <td>
                    <strong>{{ row.item.step }}</strong>
                  </td>
                  <td
                    v-for="header in eoiTableHeaders.slice(3)"
                    :key="header"
                    class="text-center"
                  >
                    <span
                      v-if="row.item[header.value]"
                      :class="
                        isColored(row.item[header.value].value)
                          ? 'colored_cell cursor-pointer'
                          : 'cursor-pointer'
                      "
                      @click="
                        openEOIDialog({
                          eoi: header.title,
                          protocol: row.item.protocol,
                          observation: row.item.observation,
                          step: row.item[header.value].description,
                          value: row.item[header.value].value,
                        })
                      "
                      >{{
                        typeof row.item[header.value].value === "boolean"
                          ? row.item[header.value].value
                            ? "Sim"
                            : "No"
                          : row.item[header.value].value
                      }}
                      <v-tooltip activator="parent" location="bottom">
                        <span
                          >({{ row.item[header.value].description }}, EOI
                          {{ header.value }})</span
                        >
                      </v-tooltip>
                    </span>
                  </td>
                </tr>
              </template>
            </v-data-table-virtual></v-card-text
          >
        </div>
      </v-expand-transition>
    </v-card>

    <v-card v-if="existsTraps" class="mt-4">
      <v-card-title class="d-flex justify-space-between align-center">
        <span>Armadilhas</span>
        <v-btn
          variant="plain"
          color="primary"
          icon
          @click="showTrap = !showTrap"
        >
          <v-icon>{{
            showTrap ? "mdi-chevron-up" : "mdi-chevron-down"
          }}</v-icon>
        </v-btn>
      </v-card-title>
      <v-expand-transition>
        <div v-show="showTrap">
          <v-card-text>
            <v-data-table-virtual
              :headers="trapTableHeaders"
              :items="trapTableItems"
            >
              <template v-slot:item="row">
                <tr>
                  <td
                    v-for="header in trapTableHeaders"
                    :key="header"
                    class="text-center"
                  >
                    <span
                      :class="
                        isColored(row.item[header.value])
                          ? 'colored_cell cursor-pointer'
                          : 'cursor-pointer'
                      "
                      @click="
                        openTrapDialog({
                          protocol: header.value,
                          value: row.item[header.value],
                        })
                      "
                      >{{
                        typeof row.item[header.value] === "boolean"
                          ? row.item[header.value]
                            ? "Sim"
                            : "No"
                          : row.item[header.value]
                      }}</span
                    >
                  </td>
                </tr>
              </template>
            </v-data-table-virtual>
          </v-card-text>
        </div>
      </v-expand-transition>
    </v-card>

    <v-card v-if="existsLab" class="mt-4">
      <v-card-title class="d-flex justify-space-between align-center"
        ><span>Filoxera</span>
        <v-btn variant="plain" color="primary" icon @click="showLab = !showLab">
          <v-icon>{{ showLab ? "mdi-chevron-up" : "mdi-chevron-down" }}</v-icon>
        </v-btn>
      </v-card-title>
      <v-expand-transition>
        <div v-show="showLab">
          <v-card-text>
            <v-data-table-virtual
              :headers="labTableHeaders"
              :items="labTableItems"
            >
              <template v-slot:item="row">
                <tr>
                  <td>
                    <strong>{{ row.item.phase }}</strong>
                  </td>
                  <td>
                    <strong>{{ row.item.observation }}</strong>
                  </td>
                  <td>
                    <strong>{{ row.item.type }}</strong>
                  </td>
                  <td
                    v-for="header in labTableHeaders.slice(3)"
                    :key="header"
                    class="text-center"
                  >
                    <span
                      :class="
                        isColored(row.item[header.value])
                          ? 'colored_cell cursor-pointer'
                          : 'cursor-pointer'
                      "
                      @click="
                        openLabDialog({
                          eoi: header.title,
                          phase: row.item.phase,
                          observation: row.item.observation,
                          type: row.item.type,
                          value: row.item[header.value],
                        })
                      "
                      >{{ row.item[header.value] }}
                    </span>
                  </td>
                </tr>
              </template>
            </v-data-table-virtual>
          </v-card-text>
        </div>
      </v-expand-transition>
    </v-card>

    <!-- EDIT DIALOGS -->
    <v-dialog v-model="eoiDialog" @afterLeave="resetEOI" width="auto">
      <v-card width="300">
        <v-card-title>{{ selectedEOI.step }}</v-card-title>
        <v-card-text>
          <v-form v-model="eoiForm">
            <v-select
              v-if="selectedEOI.isBoolean"
              v-model="selectedEOI.value"
              :items="selectItems"
              item-title="text"
              item-value="value"
              color="secondary"
              variant="outlined"
              density="compact"
              :rules="[(v) => v != null || 'Campo obrigatrio']"
            ></v-select>
            <v-text-field
              v-else
              v-model.number="selectedEOI.value"
              color="secondary"
              variant="outlined"
              density="compact"
              type="number"
              :rules="[
                (v) => !isNaN(parseInt(v)) || 'Campo obrigatrio',
                (v) => v >= 0 || 'Valor invlido',
              ]"
            ></v-text-field>
          </v-form>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="eoiDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :disabled="!eoiForm"
            :loading="isLoadingEOI"
            @click="updateEoi()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="trapDialog" @afterLeave="resetTrap" width="auto">
      <v-card width="300">
        <v-card-title>{{ selectedTrap.enemy }}</v-card-title>
        <v-card-text>
          <v-form v-model="trapForm">
            <v-text-field
              v-model.number="selectedTrap.value"
              color="secondary"
              variant="outlined"
              density="compact"
              type="number"
              :rules="[
                (v) => !isNaN(parseInt(v)) || 'Campo obrigatrio',
                (v) => v >= 0 || 'Valor invlido',
              ]"
            ></v-text-field>
          </v-form>
        </v-card-text>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="trapDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :disabled="!trapForm"
            :loading="isLoadingTrap"
            @click="updateTrap()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="labDialog" @afterLeave="resetLab" width="auto">
      <v-card width="300">
        <v-card-title>{{ selectedLab.type }}</v-card-title>
        <v-card-text>
          <v-form v-model="labForm">
            <v-text-field
              v-model.number="selectedLab.value"
              color="secondary"
              variant="outlined"
              density="compact"
              type="number"
              :rules="[
                (v) => !isNaN(parseInt(v)) || 'Campo obrigatrio',
                (v) => v >= 0 || 'Valor invlido',
              ]"
            ></v-text-field>
          </v-form>
        </v-card-text>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="labDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :disabled="!labForm"
            :loading="isLoadingLab"
            @click="updateLab()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";

export default {
  name: "VisitObservationsTables",
  props: {
    folderId: String,
    region_code: String,
    visit_date: String,
    visit_json: Object,
    lab: Object,
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();

    return { generalStore, accountStore };
  },
  data() {
    return {
      eoiDialog: false,
      selectedEOI: {},
      isLoadingEOI: false,
      eoiForm: false,
      showEOI: false,

      trapDialog: false,
      selectedTrap: {},
      isLoadingTrap: false,
      trapForm: false,
      showTrap: false,

      labDialog: false,
      selectedLab: {},
      isLoadingLab: false,
      labForm: false,
      showLab: false,

      selectItems: [
        { text: "Sim", value: true },
        { text: "No", value: false },
      ],
    };
  },
  watch: {},
  computed: {
    existsEois() {
      return this.visit_json?.eois != undefined;
    },

    existsTraps() {
      return this.visit_json?.traps != undefined;
    },

    existsLab() {
      return this.lab?.eois != undefined;
    },

    eoiTableHeaders() {
      if (!this.existsEois) return;
      let headers = [];
      headers.push({
        title: "Protocolo",
        value: "protocol",
      });
      headers.push({
        title: "Observao",
        value: "observation",
      });
      headers.push({
        title: "Etapa",
        value: "step",
      });
      headers = headers.concat(
        Object.keys(this.visit_json.eois).map((key) => {
          return {
            title: `${parseInt(key) + 1}`,
            value: `${parseInt(key) + 1}`,
            align: "center",
          };
        })
      );
      return headers;
    },
    eoiTableItems() {
      if (!this.existsEois) return;
      let items = [];
      //populate horizontal headers
      for (let [key, value] of Object.entries(this.visit_json.eois[0])) {
        for (let [key2, value2] of Object.entries(value)) {
          for (let it of value2) {
            console.log(it.description);
            let item = {};
            item.protocol = key;
            item.observation = key2;
            item.step =
              it.description != item.observation ? it.description : "---";
            items.push(item);
          }
        }
      }
      //   populate table data
      for (let [key, value] of Object.entries(this.visit_json.eois)) {
        let eoi = parseInt(key) + 1;
        for (let [key2, value2] of Object.entries(value)) {
          let protocol = key2;
          for (let [key3, value3] of Object.entries(value2)) {
            for (let obj of value3) {
              let observation = key3;
              let step =
                obj.description != observation ? obj.description : "---";
              let item = items.find(
                (element) =>
                  element.protocol == protocol &&
                  element.observation == observation &&
                  element.step == step
              );
              // process value
              let value = obj.value;
              if (isNaN(value)) {
                value =
                  value == "true" ? true : value == "false" ? false : value;
              } else {
                value = parseInt(value);
              }
              item[`${eoi}`] = {
                value: value,
                description: obj.description,
              };
            }
          }
        }
      }
      return items;
    },
    trapTableHeaders() {
      if (!this.existsTraps) return;
      let headers = [];
      headers = Object.keys(this.visit_json.traps).map((key) => {
        return {
          title: key,
          value: key,
          align: "center",
        };
      });
      return headers;
    },
    trapTableItems() {
      if (!this.existsTraps) return;
      let item = {};
      for (let [key, value] of Object.entries(this.visit_json.traps)) {
        item[key] = parseInt(value.value);
      }
      return [item];
    },
    labTableHeaders() {
      if (!this.existsLab) return;
      let headers = [];
      headers.push({
        title: "Fase",
        value: "phase",
      });
      headers.push({
        title: "Observao",
        value: "observation",
      });
      headers.push({
        title: "Tipo de Organismos",
        value: "type",
      });
      headers = headers.concat(
        Object.keys(this.lab.eois).map((key) => {
          return {
            title: `${parseInt(key) + 1}`,
            value: `${parseInt(key) + 1}`,
            align: "center",
          };
        })
      );
      return headers;
    },
    labTableItems() {
      if (!this.existsLab) return;
      let items = [];
      //populate horizontal headers
      for (let [key, value] of Object.entries(this.lab.eois[0])) {
        for (let [key2, value2] of Object.entries(value)) {
          for (let obj of value2) {
            let item = {};
            item.phase = key;
            item.observation = key2;
            item.type = obj.description;
            items.push(item);
          }
        }
      }

      //   populate table data
      for (let [key, value] of Object.entries(this.lab.eois)) {
        let eoi = parseInt(key) + 1;
        for (let [key2, value2] of Object.entries(value)) {
          for (let [key3, value3] of Object.entries(value2)) {
            for (let obj of value3) {
              let phase = key2;
              let observation = key3;
              let type = obj.description;
              let item = items.find(
                (element) =>
                  element.phase == phase &&
                  element.observation == observation &&
                  element.type == type
              );
              // process value
              let value = parseInt(obj.value);
              item[`${eoi}`] = value;
            }
          }
        }
      }
      return items;
    },
  },
  methods: {
    isColored(value) {
      if (isNaN(value)) {
        return value == "Sim" ? true : false;
      } else {
        return value > 0 ? true : false;
      }
    },
    openEOIDialog(obj) {
      this.selectedEOI = obj;
      this.selectedEOI.isBoolean = isNaN(parseInt(obj.value));
      this.selectedEOI.old_value = this.selectedEOI.value;
      this.eoiDialog = true;
    },
    resetEOI() {
      this.selectedEOI = {};
    },
    async updateEoi() {
      this.isLoadingEOI = true;
      let payload = {
        folderId: this.folderId,
        region_code: this.region_code,
        visit_date: this.visit_date,
        eoi_name: this.selectedEOI.eoi,
        protocol: this.selectedEOI.protocol,
        observation: this.selectedEOI.observation,
        step: this.selectedEOI.step,
        new_value: this.selectedEOI.value,
        isBoolean: this.selectedEOI.isBoolean,
        old_value: this.selectedEOI.old_value,
      };
      console.log(payload);

      await axios
        .request({
          url: `api/observation_update/eois`,
          method: "PATCH",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
          data: payload,
        })
        .then((response) => {
          console.log(response.data);
          this.isLoadingEOI = false;
          this.eoiDialog = false;
          this.generalStore.successSnackbar("Alterao efetuada com sucesso");
          this.$emit("refresh");
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingEOI = false;
          this.generalStore.errorSnackbar("Erro ao efetuar alterao");
        });
    },
    openTrapDialog(obj) {
      this.selectedTrap = obj;
      this.trapDialog = true;
    },
    resetTrap() {
      this.selectedTrap = {};
    },
    async updateTrap() {
      this.isLoadingTrap = true;
      let payload = {
        folderId: this.folderId,
        region_code: this.region_code,
        visit_date: this.visit_date,
        protocol: this.selectedTrap.protocol,
        new_value: this.selectedTrap.value,
      };
      console.log(payload);
      await axios
        .request({
          url: `api/observation_update/traps`,
          method: "PATCH",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
          data: payload,
        })
        .then((response) => {
          console.log(response.data);
          this.isLoadingTrap = false;
          this.trapDialog = false;
          this.generalStore.successSnackbar("Alterao efetuada com sucesso");
          this.$emit("refresh");
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingTrap = false;
          this.generalStore.errorSnackbar("Erro ao efetuar alterao");
        });
    },
    openLabDialog(obj) {
      console.log(obj);
      this.selectedLab = obj;
      this.selectedLab.old_value = this.selectedLab.value;
      this.labDialog = true;
    },
    resetLab() {
      this.selectedLab = {};
    },
    async updateLab() {
      this.isLoadingLab = true;
      let payload = {
        folderId: this.folderId,
        region_code: this.region_code,
        visit_date: this.visit_date,
        eoi_name: this.selectedLab.eoi,
        phase: this.selectedLab.phase,
        observation: this.selectedLab.observation,
        type: this.selectedLab.type,
        new_value: this.selectedLab.value,
        old_value: this.selectedLab.old_value,
      };
      console.log(payload);
      await axios
        .request({
          url: `api/observation_update/labs`,
          method: "PATCH",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
          data: payload,
        })
        .then((response) => {
          console.log(response.data);
          this.isLoadingLab = false;
          this.labDialog = false;
          this.generalStore.successSnackbar("Alterao efetuada com sucesso");
          this.$emit("refresh");
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingLab = false;
          this.generalStore.errorSnackbar("Erro ao efetuar alterao");
        });
    },
  },
};
</script>

<style scoped>
.colored_cell {
  background: #99ff00;
  padding: 5px;
}
</style>
